import uniIcon from '../../../components/uni-icon.vue';
import {
    mapState
} from 'vuex'

export default {
	components: {
		uniDrawer,
		uniIcon
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			tabIndex: 0,
			// 头部tab栏
			tabBars: [{
				name: '男孩信息',
				id: 'male'
			}, {
				name: '女孩信息',
				id: 'famale'
			}],
			rightDrawerVisible: false,
			maleList:[
				{
					headImg:'../../../static/img/person/person.png',
					name:'张三',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				},
				{
					headImg:'../../../static/img/person/person2.png',
					name:'李四',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				},
				{
					headImg:'../../../static/img/person/person3.png',
					name:'王五',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				},
				{
					headImg:'',
					name:'赵六',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				},
				{
					headimg:'',
					name:'狗蛋',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				}
			],
			famaleList:[
				{
					headImg:'../../../static/img/person/famale1.png',
					name:'王祖贤',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				},
				{
					headImg:'../../../static/img/person/famale2.png',
					name:'刘亦菲',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				},
				{
					headImg:'../../../static/img/person/famale3.png',
					name:'范冰冰',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				},
				{
					headImg:'../../../static/img/person/famale4.png',
					name:'杨超越',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				},
				{
					headimg:'../../../static/img/person/famale5.png',
					name:'安妮海瑟薇',
					timestamp:'2018-01-30 15:30',
					userid:'000001'
				}
			],
			
		}
	},
	onLoad: function() {
		
		if (!this.hasLogin) {
		        uni.showModal({
		            title: '未登录',
		            content: '您未登录，需要登录后才能继续',
		            /**
		             * 如果需要强制登录，不显示取消按钮
		             */
		            showCancel: !this.forcedLogin,
		            success: (res) => {
		                if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
		                    if (this.forcedLogin) {
		                        uni.reLaunch({
		                            url: '../../login/login'
		                        });
		                    } else {
		                        uni.navigateTo({
		                            url: '../../login/login'
		                        });
		                    }
		                }
		            }
		        });
		    }
		
	},
	methods: {
		async changeTab(e) {
			let index = e.detail.current;
			if (this.isClickChange) {
				this.tabIndex = index;
				this.isClickChange = false;
				return;
			}
			let tabBar = await this.getElSize("tab-bar"),
				tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0;

			for (let i = 0; i < index; i++) {
				let result = await this.getElSize(this.tabBars[i].id);
				width += result.width;
			}
			let winWidth = uni.getSystemInfoSync().windowWidth,
				nowElement = await this.getElSize(this.tabBars[index].id),
				nowWidth = nowElement.width;
			if (width + nowWidth - tabBarScrollLeft > winWidth) {
				this.scrollLeft = width + nowWidth - winWidth;
			}
			if (width < tabBarScrollLeft) {
				this.scrollLeft = width;
			}
			this.isClickChange = false;
			this.tabIndex = index; //一旦访问data就会出问题
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select('#' + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
		async tapTab(index) { //点击tab-bar
			if (this.tabIndex === index) {
				return false;
			} else {
				let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.tabIndex = index;
			}
			console.log(this.tabIndex);
		},
		closeRightDrawer() {
			this.rightDrawerVisible = false;
		},
		showRightDrawer() {
			this.rightDrawerVisible = true;
		},
		item1() {
			this.rightDrawerVisible = false;
			uni.showToast({
				title: 'item1'
			});
		},
		item2() {
			this.rightDrawerVisible = false;
			uni.showToast({
				title: 'item2'
			});
		},
		confirm() {
			uni.showToast({
				title: '搜索'
			})
		}
	}
}
